import { random, name, lorem, company } from 'faker';

const data = [...Array(20).keys()].map((_, index) => ({
  id: random.alphaNumeric(5),
  title: name.jobTitle(),
  description: lorem.text(),
  company: company.companyName(),
  employment_type: random.arrayElement([
    'full_time',
    'part_time',
    'contract',
    'internship',
  ]),
}));

export default data;
